.sidebar-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 20px;

  word-break: break-all;

  list-style: none;
}

.sidebar-list > li:first-child {
  margin-top: 0;
}

.sidebar-list > li.sidebar-text {
  margin-top: 20px;
  margin-left: -20px;
}

.sidebar-list > li.sidebar-item {
  margin-top: 10px;

  &:before {
    position: relative;
    top: 2px;

    display: inline-block;
    margin-right: 5px;
    margin-left: -20px;

    content: '\02022';
  }

  &.active a {
    color: var(--secondary-color);
  }
}
